        <div class="tip">
<p>在Chrome模拟器运行时你可能会发现没有效果，这是因为Chrome并不支持原生sticky实现而模拟器环境是iPhone，目前程序是根据Safari版本来判断是否原生支持sticky。因此你可以切换到Android系统进行测试，但是真机上是完全正常的交互。
<br>
<br>
你也可以设置禁用原生支持检测以获得相同的效果：<code>:check-sticky-support=&quot;false&quot;</code></p>
</div>
<p>如果你没有使用<code>100%</code>的布局，也没有头部可以直接这样使用</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">sticky</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>Blabla<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">sticky</span>&gt;</span>
</code></pre>
<p>如果你像demo一样使用<code>view-box</code>和<code>x-header</code>，那么需要这样：</p>
<pre class="hljs"><code><span class="hljs-tag">&lt;<span class="hljs-name">sticky</span> <span class="hljs-attr">scroll-box</span>=<span class="hljs-string">"vux_view_box_body"</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"46"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>Blabla<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">sticky</span>&gt;</span>
</code></pre>
<p>建议加一个<code>div</code>高度为内容高度，这样可以避免当定位为<code>sticky</code>时下面的元素会突然向上走。</p>
<pre class="hljs"><code>// 禁用原生检测时，可以在外围加
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height:44px;"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">sticky</span> <span class="hljs-attr">scroll-box</span>=<span class="hljs-string">"vux_view_box_body"</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"46"</span> <span class="hljs-attr">:check-sticky-support</span>=<span class="hljs-string">"false"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>Blabla<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">sticky</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

// 使用原生检测时，`div`紧挨着组件之后，并设置类名`vux-sticky-fill`
<span class="hljs-tag">&lt;<span class="hljs-name">sticky</span> <span class="hljs-attr">scroll-box</span>=<span class="hljs-string">"vux_view_box_body"</span> <span class="hljs-attr">:offset</span>=<span class="hljs-string">"46"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>Blabla<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">sticky</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"vux-sticky-fill"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"height:44px;"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
<div class="warning">
<p>请不要直接照复制 demo 代码，滚动容器为 window 时请不要指定 scroll-box，滚动容器并非使用 <code>view-box</code> 时 id 并不是 <code>vux_view_box_body</code>，请按照实际情况来设定。</p>
